import { request } from "./request";

//MV排行
export function getMVTop(area,limit,offset){
    return request({
        url: '/top/mv',
        params: {
            area,
            limit,
            offset
        }
    })
}

//MV评论数据
export function getMVInfo(mvid){
    return request({
        url: '/mv/detail/info',
        params: {
            mvid
        }
    })
}

//MV评论点赞转发数据
export function getMVUrl(id){
    return request({
        url: '/mv/url',
        params: {
            id
        }
    })
}

//MV评论
export function getMVComments(id,limit,offset){
    return request({
        url: '/comment/mv',
        params: {
            id,
            limit,
            offset
        }
    })
}