<template>
  <div class="singerDetail">
    <div v-show="!$store.state.showPlayMv" :class="['header',changeColor?'changeColor':'']">
      <span @click="back" class="iconfont icon-arrowLeft"></span>
      <span class="iconfont icon-liebiao-dian"></span>
    </div>
    <div class="img">
      <img :src="picUrl+'?param=500y500'" alt=""/>
    </div>
    <el-card class="card">
      <h3>{{name}}</h3>
      <span>{{imageDesc}}</span>
      <div class="focus">+关注</div>
    </el-card>
    <div class="navtab">
      <van-tabs v-model="activeName" sticky offset-top='50'>
        <van-tab title="歌曲" name="歌曲"><song class="song" /> </van-tab>
        <van-tab title="专辑" name="专辑"><album/> </van-tab>
        <van-tab title="MV" name="MV"><Mv /> </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import { getSingerDetail } from '../../NetRequest/singer'
import song from './childrenComps/song'
import album from './childrenComps/album'
import Mv from './childrenComps/Mv'
export default {
  components:{
    song,
    album,
    Mv
  },
  data() {
    return {
      info:[],
      singerId:'',
      name:'',
      picUrl:'',
      imageDesc:'',
      activeName: 'a',
      changeColor:false,
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handleScrollx, true);
    this.singerId = this.$store.state.singerId
    this.getSingerDetailData()
  },
  methods: {
    handleScrollx() {
      // console.log("滚动高度", window.pageYOffset);
      if(window.pageYOffset >= 400){
        this.changeColor = true
      }else{
        this.changeColor = false
      }
    },
    back(){
      this.$router.go(-1)
    },
    async getSingerDetailData(){
      const res = await getSingerDetail(this.singerId)
      // console.log(res);
      let result = res.data.data
      this.name = result.artist.name
      this.picUrl = result.artist.cover
      this.imageDesc = result.identify.imageDesc
    }
  },
  beforeDestroy() {
    window.removeEventListener("scroll",this.handleScrollx, true);
  },
}
</script>

<style scoped>
.header{
  position: fixed;
  top: 0;
  display: flex;
  justify-content: space-between;
  width: 95%;
  height: 20px;
  padding: 15px;
  line-height: 20px;
  text-align: center;
  z-index: 99;
  /* background-color: aqua; */
}
.changeColor{
  background-color: #fff;
}

.header .iconfont{
  font-size: 25px;
  color: #fff;
}
.changeColor .iconfont{
  font-size: 25px;
  color: #999;
}
img{
  position: absolute;
  width: 100%;
  height: 50%;
  z-index: -1;
}
.card{
  position: absolute;
  top: 45%;
  left: 10%;
  height: 25%;
  width: 80%;
  z-index: 1;
  text-align: center;
}
.focus{
  height: 30px;
  width: 80px;
  margin: 20px auto;
  border-radius: 20px;
  background-color: rgb(253,58,56);
  line-height: 30px;
  text-align: center;
  color: #fff;
}
.navtab{
  position: absolute;
  width: 100%;
  top: 72%;
  z-index: -1;
}
</style>
