import { request } from "./request";

//歌手详情
export function getSingerDetail(id){
    return request({
        url:'/artist/detail',
        params:{
            id
        }
    })
}

//歌手分类列表
export function getSingerCategoryList(type,area,initial){
    return request({
        url:'/artist/list',
        params:{
            type,
            area,
            initial
        }
    })
}

//歌手热门歌曲
export function getSingerHotSongs(id){
    return request({
        url:'/artist/top/song',
        params:{
            id
        }
    })
}

//歌手专辑
export function getSingerAlbum(id,limit){
    return request({
        url:'/artist/album',
        params:{
            id,
            limit
        }
    })
}

//歌手Mv
export function getSingerMv(id){
    return request({
        url:'/artist/mv',
        params:{
            id
        }
    })
}