<template>
  <div class="album">
    <van-loading
      class="loading"
      text-color="#999"
      v-if="albumList == ''"
      color="red"
      type="spinner"
      size="24px"
      >正在加载...</van-loading
    >
      <div v-for="item in albumList" :key="item.id" class="ablum-item">
        <div class="img">
          <img :src="item.picUrl+'?param=100y100'" />
        </div>
        <div class="ablum-text">
          <p class="name">{{ item.name }}</p>
          <p class="info">
            {{ item.publishTime | getTime("YYYY-MM-DD") }}
          </p>
        </div>
      </div>
    <div v-if="albumList!=''" class="more">
      查看更多请前往网易云APP~~
    </div>
  </div>
</template>

<script>
import { getSingerAlbum } from "../../../NetRequest/singer";
export default {
  data() {
    return {
      albumList: [],
      singerId: "",
    };
  },
  mounted() {
    this.singerId = this.$store.state.singerId;
    setTimeout(() => {
      this.getAlbumList();
    }, 1000);
  },
  methods: {
    async getAlbumList() {
      const res = await getSingerAlbum(this.singerId, 20)
        // console.log(res);
        for (const item of res.data.hotAlbums) {
        this.albumList.push({
          id: item.id,
          name: item.name,
          picUrl: item.picUrl,
          publishTime: item.publishTime,
        });
      }
    },
  },
};
</script>

<style scoped>
.album {
  margin: 20px 15px 50px 15px;
}
p {
  height: 20px;
  margin: 5px;
  max-width: 180px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.loading {
  margin-top: 20px;
  margin-left: 40%;
}
.ablum-item {
  display: flex;
  justify-content: left;
  margin-bottom: 15px;
}
.ablum-item .img img {
  width: 100px;
  height: 100px;
  border-radius: 10px;
}
.ablum-text{
    margin-top: 30px;
}
.ablum-text .info {
  font-size: 13px;
  color: rgb(149, 149, 149);
}
.more{
  width: 100%;
  height: 30px;
  line-height: 30px;
  text-align: center;
  margin-bottom: 50px;
  color: #999;
}
</style>