<template>
  <div class="songs">
    <div class="playBtn">
      <div class="left">
        <div class="left-icon">
          <span @click="playAll" class="iconfont icon-caret_right"></span>
        </div>
        <span>播放全部</span
        ><span class="list-count">({{ songList.length }})</span>
      </div>
      <div class="right">
        <span class="iconfont icon-download"></span>
        <span class="iconfont icon-icon-test"></span>
      </div>
    </div>
    <div v-for="(item, index) in songList" :key="index" class="song">
      <div class="item">
        <div class="left" @click="playThis(item.id, index)">
          <div class="text">
            <span>{{ index + 1 }}</span>
          </div>
          <div class="track">
            <p class="name">{{ item.name }}</p>
            <p class="singer">{{ artistsList[index] }}</p>
          </div>
        </div>
        <div class="icon">
          <span class="iconfont icon-bofang"></span>
          <span class="iconfont icon-liebiao-dian"></span>
        </div>
      </div>
    </div>
    <div v-if="songList!=''" class="more">查看更多请前往网易云APP~~</div>
  </div>
</template>

<script>
import { getSingerHotSongs } from "../../../NetRequest/singer";
import { findItem } from "../../../common/common";
export default {
  data() {
    return {
      songList: [],
      singerId: "",
      artistsList: [],
      hasClcikMusicId: "",
    };
  },
  mounted() {
    this.singerId = this.$store.state.singerId;
    this.getSingerHotSongsData();
  },
  methods: {
    async getSingerHotSongsData() {
      const res = await getSingerHotSongs(this.singerId);
      // console.log(res);
      for (const item of res.data.songs) {
        this.songList.push({
          id: item.id,
          name: item.name,
          artists: item.ar[0].name,
          picUrl: item.al.picUrl,
        });
      }
      for (let i = 0; i < this.songList.length; i++) {
        let result = [];
        for (const item of res.data.songs[i].ar) {
          result.push(item.name);
          this.artistsList[i] = result.join(" / ");
        }
      }
    },
    /* 点击播放歌曲 */
    playThis(musicId, i) {
      // console.log(musicId);
      this.$store.state.playShow = true;
      const index = findItem(this.$store.state.playList, "id", musicId);
      if (index != -1) {
        //如果播放列表有这首歌
        if (this.hasClcikMusicId == musicId) {
          //如果被点击这首歌已经被点击过且正在播放
          return;
        }
        this.$bus.$emit("receiveMusicId", musicId, index);
      } else {
        //播放列表没有这首歌
        const newIndex = this.$store.state.playList.length;
        this.$store.state.playList.push(this.songList[i]);
        // console.log(this.$store.state.playList);
        this.$bus.$emit("receiveMusicId", musicId, newIndex);
        this.hasClcikMusicId = musicId;
      }
    },
    /* 播放全部 */
    playAll(){
      this.$store.state.playList=this.songList
      this.$toast('已添加到播放列表');
      setTimeout(()=>{
        this.$store.state.playShow = true
        this.$bus.$emit('receiveMusicId',this.songList[0].id,0)
        },1000)
      localStorage.removeItem('newSong')
      localStorage.setItem("newSong", JSON.stringify(this.songList));
    }
  },
};
</script>

<style scoped>
.songs{
  margin-bottom: 60px;
}
.playBtn {
  font-size: 14px;
  display: flex;
  margin-top: 20px;
  justify-content: space-between;
  background-color: #fff;
}
.playBtn .left {
  display: flex;
  justify-content: left;
}
.left-icon {
  width: 18px;
  height: 18px;
  line-height: 18px;
  text-align: center;
  margin-right: 10px;
  border-radius: 50%;
  background-color: red;
}
.list-count {
  position: relative;
  top: 3px;
  left: 10px;
  color: #aaa;
  font-size: 13px;
}
.icon-caret_right {
  font-size: 10px;
  color: #eee;
}
.icon-download {
  font-size: 20px;
  margin-right: 20px;
}
.icon-icon-test {
  margin-right: 10px;
}
.song {
  padding: 0 15px;
}
.song .item {
  width: 100%;
  display: flex;
  justify-content: space-between;
  height: 60px;
}
.song p {
  height: 20px;
  margin: 5px;
  max-width: 150px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.item .text {
  width: 20px;
  line-height: 60px;
  text-align: center;
  margin-right: 20px;
  color: #999;
}
.item .left {
  display: flex;
  justify-content: left;
}
.track {
  text-align: left;
}
.singer {
  font-size: 13px;
  color: rgb(149, 149, 149);
}
.icon {
  line-height: 60px;
  text-align: center;
}
.icon .iconfont:nth-child(1) {
  margin-right: 15px;
}
.icon .iconfont {
  font-size: 20px;
  color: #999;
}
.more {
  width: 100%;
  height: 30px;
  line-height: 30px;
  text-align: center;
  margin-bottom: 50px;
  color: #999;
}
</style>