<template>
  <div class="palyMv" :style="{ height: screenHeight + 'px' }">
    <div class="header">
      <span @click="back" class="iconfont icon-arrowLeft"></span>
      <span class="iconfont icon-MV"></span>
      <div class="right">
        <span class="iconfont icon-xiaoping"></span>
        <span class="iconfont icon-liebiao-dian"></span>
      </div>
    </div>
    <div class="video">
      <video id="video" autoplay controls preload :src="mvUrl" height="230" width="100%"/>
      <div class="fz">
        <span class="iconfont icon-fanzhuanpingmu"></span><br />
      </div>
    </div>
    <div class="icon">
      <span class="iconfont icon-dianzan1"></span><br /><span>{{
        likedCount
      }}</span
      ><br />
      <span class="iconfont icon-pinglun1"></span><br /><span>{{
        commentCount
      }}</span
      ><br />
      <span class="iconfont icon-fenxiang2"></span><br /><span>{{
        shareCount
      }}</span
      ><br />
      <span class="iconfont icon-tianjiashoucang"><br /></span><span>收藏</span>
    </div>
    <div class="info">
      <div class="auther">
        <img class="avator" :src="avatarUrl+'?param=100y100'" alt="" />&emsp;
        <p class="artistName">{{ mvInfo.artistName }}</p>
      </div>
      <br />
      <!-- <div class="jia">+</div> -->
      <p class="name">{{ mvInfo.name }}</p>
      <br />
      <span class="count">{{ mvInfo.playCount }}次观看</span>
    </div>
    <div class="divider"></div>
    <div class="footer">
      <span class="iconfont icon-xin"></span>
      <p class="name2">{{ mvInfo.name }}-{{ mvInfo.artistName }}</p>
    </div>
  </div>
</template>

<script>
import { getMVInfo, getMVUrl } from "../../NetRequest/MV";
import { toStringNum } from "../../common/common.js";
import { getUserDetail } from "../../NetRequest/user";
// import { MESSAGE_TYPE } from 'vue-baberrage'
export default {
  data() {
    return {
      screenHeight: "",
      avatarUrl: "",
      mvInfo: [],
      shareCount: "",
      commentCount: "",
      likedCount: "",
      mvUrl: "",
      isSend: false,
    };
  },
  mounted() {
    window.onresize = function () {
      this.screenHeight = window.screen.height;
    };
    this.mvInfo = this.$store.state.palyMvInfo;
    // let timer = new Date().getTime()
    // console.log('mounted',timer)
  },
  methods: {
    getData(){
      // console.log('###')
      this.getUserAvatar();
    this.getMVInfoData();
    this.getMVUrlData();
    // this.getMVCommentsData()
    },
    back() {
      this.$store.state.showPlayMv = false;
      this.$bus.$emit("closeModle");
      this.isSend = true;
      this.$store.state.showControlBar = true
    },
    async getUserAvatar() {
      try {
        const { data: res } = await getUserDetail(this.mvInfo.id);
          // console.log(res);
        this.avatarUrl = res.profile.avatarUrl;
      } catch (e) {
        // console.log(e);
        console.log("用户头像请求失败，已启用默认头像");
        this.avatarUrl =
          "https://img0.baidu.com/it/u=797324285,1943411797&fm=26&fmt=auto&gp=0.jpg";
      }
    },
    async getMVInfoData() {
      const res = await getMVInfo(this.mvInfo.id);
      // console.log(res);
      this.likedCount = toStringNum(res.data.likedCount);
      this.commentCount = toStringNum(res.data.commentCount);
      this.shareCount = toStringNum(res.data.shareCount);
    },
    async getMVUrlData() {
      const res = await getMVUrl(this.mvInfo.id);
      // console.log(res);
      this.mvUrl = res.data.data.url;
    },
    // async getMVCommentsData() {
      // const res = await getMVComments(this.mvInfo.id);
      // console.log(res);
      // this.mvUrl = res.data.data.url;
    // },
  },
  beforeDestroy() {
    // window.removeEventListener("resize",true);
    // console.log('被销毁了')
  },
  computed: {
    isPlayMv() {
      return this.$store.state.showPlayMv;
    },
  },
  watch: {
    isPlayMv() {
      this.mvInfo = this.$store.state.palyMvInfo;
      if(!this.$store.state.showPlayMv){
        // console.log('NoshowPlayMv');
        let video = document.querySelector('#video')
        video.pause()
      }
      
    },
  },
};
</script>

<style scoped>
.palyMv {
  /* position: fixed; */
  /* width: 100%; */
  height: 100%;
  background-color: #333;
}
.header {
  display: flex;
  justify-content: space-between;
  /* background-color: aqua; */
  padding: 15px;
}
.iconfont {
  color: #eee;
}
.icon-arrowLeft,
.icon-MV {
  font-size: 25px;
}
.icon-MV {
  font-size: 30px;
}
.icon-liebiao-dian {
  font-size: 20px;
  margin-left: 20px;
}
.video {
  position: absolute;
  width: 100%;
  top: 27%;
}
.fz {
  position: absolute;
  right: 20px;
  top: 38%;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  background-color: rgb(0, 0, 0, 0.3);
  border-radius: 50%;
  /* border: 1px red solid; */
}
.icon-fanzhuanpingmu {
  font-size: 20px;
}
.icon {
  position: absolute;
  top: 61%;
  right: 20px;
  width: 30px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  color: #999;
  font-size: 13px;
}
.icon-dianzan1,
.icon-fenxiang2,
.icon-tianjiashoucang {
  font-size: 20px;
}
.info {
  position: absolute;
  top: 75%;
  margin-left: 20px;
}
.auther {
  display: flex;
  justify-content: left;
  line-height: 40px;
  text-align: center;
  color: #eee;
}
.avator {
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.info .name {
  color: #999;
}
.info .count {
  font-size: 13px;
  color: #666;
}
.jia {
  position: absolute;
  right: 0px;
  top: 10px;
  background-color: rgb(251, 100, 65);
  height: 20px;
  width: 30px;
  margin: 0;
  line-height: 20px;
  text-align: center;
  color: #eee;
  border-radius: 15px;
}
.divider {
  position: absolute;
  top: 93%;
  height: 1px;
  width: 100%;
  background-color: #fff;
  /* border-top: 1px,#999 solid */
}
.footer {
  position: absolute;
  top: 95%;
  display: flex;
  justify-content: left;
  margin-left: 15px;
  color: #999;
}
.icon-xin {
  font-size: 20px;
  margin-right: 15px;
}
.name,
.name2,
.artistName {
  margin: 0;
  max-width: 270px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>