<template>
  <div class="mv">
    <div class="content">
      <div class="item" v-for="(item,index) in mvList" :key="item.id">
        <img @click="playMv(index)" :src="item.picUrl+'?param=200y200'" />
        <p>{{item.name}}</p>
        <span class="time">03:54</span>
        <span class="iconfont icon-caret-right">{{item.playCount}}</span>
      </div>
    </div>
    <div v-if="mvList!=''" class="more">
      查看更多请前往网易云APP~~
    </div>
    <van-popup
      :style="{ height: '100%', width: '100%' }"
      v-model="$store.state.showPlayMv"
    >
      <playMv v-if="$store.state.showPlayMv" ref="getData"/>
    </van-popup>
  </div>
</template>

<script>
import { getSingerMv } from "../../../NetRequest/singer";
import { toStringNum } from "../../../common/common.js"
import playMv from "../../../components/MvTopMore/playMv";
export default {
  components: { playMv },
  data() {
    return {
      mvList: [],
      singerId: "",
      topOffset:''
    };
  },
  mounted() {
    this.singerId = this.$store.state.singerId;
    this.getSingerMvData();
     this.$bus.$on("closeModle", this.closeModle);
  },
  methods: {
    async getSingerMvData() {
      const res = await getSingerMv(this.singerId);
      // console.log(res);
          for (const item of res.data.mvs) {
          this.mvList.push({
            id: item.id,
            name: item.name,
            picUrl: item.imgurl,
            artistName:item.artistName,
            playCount: toStringNum(item.playCount),
            duration:item.duration
          });
        }
    },
    playMv(index) {
      // console.log(index);
      this.$store.state.isPlaying = true
      this.$bus.$emit('handlePlay')
      this.$store.state.palyMvInfo = this.mvList[index];
      this.$store.state.showPlayMv = true;
      this.stopBodyScroll(true);
      setTimeout(()=>{
        // console.log('setTimeout')
        this.$refs.getData.getData()
      },1500)
      document.body.addEventListener("touchmove", this.bodyScroll, {
        passive: false,
      });
      document.querySelector("body").style.position = "fixed";
      document.querySelector("body").style.width = "100%";
      this.$store.state.showControlBar = false
    },
    bodyScroll(event) {
      event.preventDefault();
    },
    stopBodyScroll(isFixed) {
      let body_scroll = document.querySelector("body");
      if (isFixed) {
        this.topOffset = window.scrollY;
        body_scroll.style.position = "fixed";
        body_scroll.style.top = -this.topOffset + "px";
      } else {
        body_scroll.style.position = "";
        window.scrollTo(0, this.topOffset);
      }
    },
    closeModle() {
      // console.log('closeModle');
      this.stopBodyScroll(false);
      document.body.removeEventListener("touchmove", this.bodyScroll, {
        passive: false,
      });
      document.querySelector("body").style.position = "initial";
      document.querySelector("body").style.height = "auto";
    },
  },
  beforeDestroy() {
    this.$bus.$off('closeModlecloseModle')
  },
};
</script>

<style scoped>
.mv {
  padding: 15px;
}
.content{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.item{
  position: relative;
    padding: 10px;
}
img {
  width: 140px;
  height: 150px;
}
p {
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 140px;
  margin: 0;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  font-size: 13px;
}
.time{
  position: absolute;
  right: 10px;
  bottom: 70px;
  font-size: 12px;
  color: aliceblue;
}
.iconfont{
  font-size: 13px;
}
.more{
  width: 100%;
  height: 30px;
  line-height: 30px;
  text-align: center;
  margin-bottom: 50px;
  color: #999;
}
</style>